import React, { useState, useEffect, useContext } from 'react';
import "./Order.css"
import Button from '../FormElements/Button'
import LoadingSpinner from "../UIElements/LoadingSpinner"
import { useHttpClient } from '../hooks/http-hook';
import ErrorModal from "../UIElements/ErrorModal";
import { CartContext } from '../../context/cart-context';
import Card from '../UIElements/Card';
import { Link, useHistory } from 'react-router-dom';
import { useStripe } from '@stripe/react-stripe-js'

import {
    VALIDATOR_REQUIRE,
    VALIDATOR_EMAIL
  } from '../util/validators';
  import Input from '../FormElements/Input';

import { useForm } from "../hooks/form-hook"

import Geocode from "react-geocode";

const Order = () => {
    

    const [items, setItems] = useState([])
    const [orderValid, setOrderValid] = useState(false)
    const { isLoading, error, sendRequest, clearError } = useHttpClient();
    const [minOrderValue, setMinOrderValue] = useState(false);
    const { cartItems, itemCount, removeProduct, increase, decrease, total } = useContext(CartContext);
    const stripe = useStripe();
    let history = useHistory();


    const [formState, inputHandler] = useForm(
        {
        //   name: {
        //     value: '',
        //     isValid: false
        // },
            street: {
                value: '',
                isValid: false
        },
            local: {
                value: '',
                isValid: false
        },
            apartament: {
                value: null,
                isValid: true
        },
            zipCode_start: {
                value: '',
                isValid: false
        },
            zipCode_end: {
                value: '',
                isValid: false
        },
            city: {
                value: '',
                isValid: false
        },
            email: {
                value: '',
                isValid: false
        },
            phoneNumber: {
                value: '',
                isValid: false
        }
        },
        false
      );

    const formSubmitHandler = async event => {
        event.preventDefault();
        
        let customer_items = cartItems.map(i => {
            return {
                quantity: i.quantity,
                name: i.name,
                price: i.price
            }
        })
        
        const line_items = cartItems.map(i => {
            return {
                quantity: i.quantity,
                price_data: {
                    currency: "pln",
                    unit_amount: i.price * 100,
                    product_data: {
                        name: i.name,
                        description: i.description
                    }
                }  
            }
        })
        let address = {
            street: formState.inputs.street.value,
            local: formState.inputs.local.value,
            zipCode_start: formState.inputs.zipCode_start.value,
            zipCode_end: formState.inputs.zipCode_end.value,
            city: formState.inputs.city.value
        }
        if (formState.inputs.apartament.value) {
            address = {
                ...address,
                apartament: formState.inputs.apartament.value
            }
        }
        if (event.target.id === "payment-online") {
            try {
                const responseData = await sendRequest(
                    `${process.env.REACT_APP_BACKEND_URL}/api/checkout/`,
                    'POST',
                    JSON.stringify({
                        line_items,
                        customer_email: formState.inputs.email.value
                    }),
                    {
                      'Content-Type': 'application/json'
                    }
                );
                const { sessionId } = responseData
                await stripe.redirectToCheckout({
                    sessionId
                })
            } catch (err) {}
        } else if (event.target.id === "payment-offline") {
            try { 
                await sendRequest(
                    `${process.env.REACT_APP_BACKEND_URL}/api/mail/`,
                    'POST',
                    JSON.stringify({
                        customer_email: formState.inputs.email.value,
                        customer_phoneNumber: formState.inputs.phoneNumber.value,
                        customer_address: address,
                        customer_items,
                        total
                    }),
                    {
                      'Content-Type': 'application/json'
                    }
                );
                await history.push("/success")
                window.location.reload()
              } catch (err) {}
        } 
      };
    
    return (
        <React.Fragment>
        {isLoading && <LoadingSpinner asOverlay />}
        <ErrorModal error={error} onClear={clearError} />
        <div className="order">
                <h1>Uzupenij dane do zamwienia</h1>
                <Card>
                <form
                // onSubmit={formSubmitHandler}
                >
                    {/* <h3>Wypenij dane do zamwienia</h3> */}
                    <div className="address-street">
                    <Input 
                    id="street"
                    element="input"
                    type="text"
                    label="Ulica"
                    validators={[VALIDATOR_REQUIRE()]}
                    errorText="Wprowad poprawn nazw ulicy."
                    onInput={inputHandler}
                    />
                    <Input 
                    id="local"
                    element="input"
                    type="text"
                    label="Nr lokalu"
                    validators={[VALIDATOR_REQUIRE()]}
                    errorText="Podaj poprawny nr lokalu."
                    onInput={inputHandler}
                    />
                    <Input 
                    id="apartament"
                    element="input"
                    type="number"
                    label="Nr mieszkania"
                    validators={null}
                    initialValid={true}
                    // validators={[VALIDATOR_REQUIRE()]}
                    // errorText="Podaj poprawny nr mieszkania."
                    onInput={inputHandler}
                    />
                    </div>
                    <div className="address-city">
                        <div className="address-city__zip" style={{position: 'relative'}}>
                            <div className="address-city__zip-label">
                                <label>Kod pocztowy</label>
                            </div>
                            <div className="address-city__zip-input">
                            <Input 
                                id="zipCode_start"
                                element="input"
                                type="text"
                                // label="Kod pocztowy"
                                validators={[VALIDATOR_REQUIRE()]}
                                errorText={formState.inputs.zipCode_end.isValid && "Podaj poprawny kod pocztowy."}
                                onInput={inputHandler}
                            />
                            <span>-</span>
                            <Input 
                                id="zipCode_end"
                                element="input"
                                type="text"
                                // label="Nr lokalu"
                                validators={[VALIDATOR_REQUIRE()]}
                                errorText={formState.inputs.zipCode_start.isValid && "Podaj poprawny kod pocztowy."}
                                onInput={inputHandler}
                            />
                            </div>
                        </div>
                    <div className="address-city__city">
                    <Input 
                    id="city"
                    element="input"
                    type="text"
                    label="Miasto"
                    validators={[VALIDATOR_REQUIRE()]}
                    errorText="Podaj poprawn nazw miasta."
                    onInput={inputHandler}
                    />
                    </div>
                    
                    </div>
                    <Input 
                    id="email"
                    element="input"
                    type="email"
                    label="Email"
                    validators={[VALIDATOR_REQUIRE(), VALIDATOR_EMAIL]}
                    errorText="Podaj poprawny email."
                    onInput={inputHandler}
                    />
                    <Input 
                    id="phoneNumber"
                    element="input"
                    type="text"
                    label="Numer telefonu"
                    validators={[VALIDATOR_REQUIRE()]}
                    errorText="Podaj nr telefonu."
                    onInput={inputHandler}
                    />
                    <Button 
                    // type="submit"
                    disabled={!formState.isValid}
                    id="payment-offline"
                    onClick={formSubmitHandler}
                    >
                    PATNO PRZY ODBIORZE
                    </Button>
                    <Button 
                    // type="submit"
                    disabled={!formState.isValid}
                    id="payment-online"
                    onClick={formSubmitHandler}
                    >
                    PATNO ONLINE
                    </Button>
                    {/* <button onClick={() => console.log(formState.inputs)
                     }></button> */}
                </form>
                </Card>
            {/* {!items.length > 0 && <h3 style={{color: "white"}}>Brak produktw w koszyku</h3>}
            {items.length > 0 && <Button disabled={!orderValid}>Przejd do zamwienia</Button>}
            {!items.length > 0 && <Link to="/"><Button>Przejd do sklepu</Button></Link>} */}
        </div>
        </React.Fragment>
        
    )
}

export default Order
